import React, { useState, useLayoutEffect } from "react";
import { Link } from "react-router-dom";
import Logo from "../../assets/Logo.png";
import FB from "../../assets/facebook-blanco.png";
import IG from "../../assets/insta-blanco.png";
import Twitter from "../../assets/twitter-blanco.png";
import axios from "axios";

import Cross from "../../assets/Cerrar menú.png";
import Menu from "../../assets/Menú.png";
import ReactGA from "react-ga";

const Blogs = () => {
  const [posts, setPosts] = useState([]);

  useLayoutEffect(() => {
    window.scrollTo(0, 0);
    loadNews();
  }, []);

  function loadNews() {
    axios
      .get(
        "http://public-api.wordpress.com/rest/v1/sites/procorlab903697760.wordpress.com/posts"
      )
      .then((res) => {
        setPosts(res.data.posts);
        console.log(posts);
      })
      .catch((error) => console.log(error));
  }

  function close() {
    var x = document.getElementById("myLinks");
    var y = document.getElementById("topnav");
    y.classList.remove("open");
    var ham = document.getElementById("ham");
    var cross = document.getElementById("cross");
    var carrito = document.getElementById("icon");
    var carro = document.getElementById("carrito");
    var logo1 = document.getElementById("logo__nav1");
    var logo2 = document.getElementById("logo__nav2");
    if (x.style.display === "block") {
      logo1.style.display = "block";
      logo2.style.display = "none";
      x.style.display = "none";
      cross.style.display = "none";
      ham.style.display = "block";
      carrito.style.display = "block";
      carrito.classList.remove("open");
      carro.style.display = "block";
    }
  }
  function handleClick(e) {
    e.preventDefault();

    var x = document.getElementById("myLinks");
    var y = document.getElementById("topnav");
    y.classList.toggle("open");
    var menuOpen = document.getElementById("topnav");
    var logo1 = document.getElementById("logo__nav1");
    var logo2 = document.getElementById("logo__nav2");
    var ham = document.getElementById("ham");
    var cross = document.getElementById("cross");
    var carrito = document.getElementById("icon");
    var carro = document.getElementById("carrito");

    /*Hide logo and display the other*/

    /*Hide and show menu*/
    if (x.style.display === "block") {
      x.style.display = "none";
    } else {
      x.style.display = "block";
    }
  }

  function createMarkup(e) {
    return { __html: e };
  }

  return (
    <div id="blogs">
      <div className="topnav" id="topnav">
        <div className="menu">
          <a>
            <Link className="active logo__procor" to="/">
              <img id="logo__nav1" className="logo__green" src={Logo} />
            </Link>
          </a>
          <div id="menu" className="menu-home">
            <a>
              <Link className="Nav__link" to="/home">
                PRUEBAS
              </Link>
            </a>
            <a>
              <Link className="Nav__link" to="/blogs">
                BLOG
              </Link>
            </a>
            <a>
              <Link className="Nav__link" to="/citas">
                CITAS
              </Link>
            </a>
            <a>
              <Link className="Nav__link" to="/faqs">
                FAQS
              </Link>
            </a>
          </div>
          <div>
            <a id="menu-empresa">
              <Link className="Nav__link" to="/procor-empresas">
                IR A PERSONAS
              </Link>
            </a>
          </div>
          <div id="myLinks" className="hamHome">
            <a onClick={handleClick}>
              <Link className="Nav__link" to="/home">
                Pruebas
              </Link>
            </a>
            <a onClick={handleClick}>
              <Link className="Nav__link" to="/blogs">
                Blogs
              </Link>
            </a>
            <a onClick={handleClick}>
              <Link className="Nav__link" to="/citas">
                Citas
              </Link>
            </a>
            <a onClick={handleClick}>
              <Link className="Nav__link" to="/faqs">
                FAQS
              </Link>
            </a>
            <a onClick={handleClick}>
              <Link className="Nav__link" to="/procor-empresas">
                Ir a personas
              </Link>
            </a>

            <div className="logos_navbar">
              {/* <a href="#about"><img src={Whatsapp} /></a> */}
              <a href="https://www.facebook.com/Procorlab-100801542046256/">
                <img src={FB} />
              </a>
              <a href="https://instagram.com/procorlab?igshid=15t963ppv1gvc">
                <img src={IG} />
              </a>
              <a href="https://twitter.com/Procorlab">
                <img src={Twitter} />
              </a>
            </div>
          </div>

          <a id="icon" className="icon" onClick={handleClick}>
            <img id="ham" className="hamburger" src={Menu} />
            <img id="cross" className="cross" src={Cross} />
          </a>
        </div>
      </div>
      <div id="politicas">
        <div className="columnaNoticias">
          {posts?.length > 0 ? (
            posts.map((elm, idx) => (
              <Link
                key={idx}
                to={`/blogs/${elm.title}/${elm.ID}`}
                style={{ textDecoration: "none" }}
              >
                <div className="noticias-p">
                  <div
                    style={{ position: "relative", textalign: "center" }}
                    id="blog__destacadas"
                  >
                    <div
                      className="newsCard"
                      style={{ backgroundImage: `url(${elm.featured_image})` }}
                    ></div>
                    <div className="newsCardTitle">{elm.title}</div>
                  </div>
                </div>
              </Link>
            ))
          ) : (
            <div className="noPosts"> No hay entradas relacionadas</div>
          )}
        </div>
      </div>
    </div>
  );
};

export default Blogs;
