import React, { useState, useLayoutEffect } from "react";

import { Link, withRouter, useHistory } from "react-router-dom";
import Cookies from "universal-cookie";

const CookieAlert = (props) => {
  const { location } = props;
  const cookies = new Cookies();

  useLayoutEffect(() => {
    setCookieClass("cookie__modal");
    checkCookies();
    return () => {};
  }, []);
  const [cookieClass, setCookieClass] = useState(" ");

  let history = useHistory();

  function checkCookies() {
    if (cookies.getAll([]).expirery === "expirery") {
      setCookieClass("hideCookieAlert");
    }
  }

  function handleAlert() {
    setCookieClass("hideCookieAlert");
    cookies.set("expirery", "expirery", {
      path: "/",
      expires: new Date(Date.now() + 2592000),
    });
  }

  return (
    <div className={cookieClass}>
      <div className="cookie__block">
        <div className="cookie__info">
          En ProcorLab utilizamos cookies propias y de terceros para asegurarnos
          de que todo funcione correctamente, ofrecerte un mejor servicio y
          mostrarte recomendaciones y anuncios ajustados a tus preferencias.
          Para más información, visita nuestra
          <a href="/aviso-cookies">
            <button onClick={() => handleAlert()}>política de cookies</button>
          </a>
        </div>
        <div className="cookie__options">
          <button className="green_button" onClick={() => handleAlert()}>
            Aceptar cookies
          </button>
          <button
            style={{ marginTop: 15 }}
            className="red_button"
            onClick={() => history.goBack()}
          >
            Rechazar cookies
          </button>
        </div>
      </div>
    </div>
  );
};

export default withRouter(CookieAlert);
